
.header-enter-active,
.header-leave-active {
  transition: all 0.5s;
}
.header-enter,
.header-leave-active {
  margin-top: -3.75rem;
}
#header {
  box-sizing: border-box;
  padding: 0 1rem;
  height: 3.75rem;
  line-height: 3.75rem;
  background-color: #fff;
}
#header .search {
  float: left;
}
#header .nav {
  float: right;
  margin: 0;
  padding: 0;
}
#header .nav .el-dropdown-link {
  position: relative;
  display: inline-block;
  padding-left: 3.125rem;
  color: #333;
  cursor: pointer;
  vertical-align: middle;
}
#header .nav .user-avatar {
  position: absolute;
  left: 0;
  top: 0.625rem;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
}


