
.menu-enter-active,
.menu-leave-active {
  transition: all 0.5s;
}
.menu-enter,
.menu-leave-active {
  margin-left: -8.75rem;
}
#wl-menu {
  width: 8.75rem;
  min-width: 8.75rem;
  background-color: #35404c;
  overflow: auto;
}
#wl-menu .wl-menu-logo {
  width: 8.75rem;
  height: 3.75rem;
}
#wl-menu .wl-menu-logo__wrapper {
  position: fixed;
  z-index: 1;
  display: block;
  width: 8.75rem;
  height: 3.75rem;
  line-height: 3.75rem;
  color: #fff;
  background-color: #04a6e9;
  font-size: 18px;
  text-align: center;
}
#wl-menu .wl-menu-logo__wrapper img {
  vertical-align: middle;
  font-size: 12px;
  line-height: 1;
}
#wl-menu .el-menu--dark {
  background-color: #35404c;
}
#wl-menu .el-menu--dark .el-submenu .el-menu {
  background-color: #35404c;
}
#wl-menu .el-menu-item,
#wl-menu .el-submenu__title {
  height: 2.812rem;
  line-height: 2.812rem;
}
#wl-menu .el-menu--horizontal.el-menu--dark .el-submenu .el-menu-item.is-active,
#wl-menu .el-menu-item.is-active {
  color: #fff;
  background-color: #2a333c;
}
#wl-menu .el-menu--dark .el-menu-item:hover,
#wl-menu .el-menu--dark .el-submenu__title:hover {
  background-color: #2a333c;
}


